@font-face {
   font-family: Cocogoose Narrow;
   src: url("../fonts/CocogooseNarrow_Letterpress/CocogooseNarrow_Letterpress.eot");
   src: local("CocogooseNarrow_Letterpress"),
      local("../fonts/CocogooseNarrow_Letterpress/CocogooseNarrow_Letterpress"),
      url("../fonts/CocogooseNarrow_Letterpress/CocogooseNarrow_Letterpress.eot?#iefix")
         format("embedded-opentype"),
      url("../fonts/CocogooseNarrow_Letterpress/CocogooseNarrow_Letterpress.woff")
         format("woff"),
      url("../fonts/CocogooseNarrow_Letterpress/CocogooseNarrow_Letterpress.ttf")
         format("truetype");
   font-weight: 900;
   font-style: normal;
   font-display: swap;
}
